@import url(https://fonts.googleapis.com/css2?family=Public+Sans:wght@300;400;500;600;700&display=swap);
.Scrollbar{overflow-y:auto;scrollbar-width:thin}.Scrollbar::-webkit-scrollbar{width:6px;height:6px}.Scrollbar::-webkit-scrollbar-thumb{border-radius:3px;background-color:#b5b6b700}.Scrollbar:hover::-webkit-scrollbar-thumb{background-color:#c2c3c47f}

.Row-avatar{width:40px;height:40px;border-radius:20px}

.DataTable{position:relative}.DataTable-loading{width:100%;display:flex;align-items:center;justify-content:center}.DataTable-loading td{color:#777}.DataTable-loading.has-items{position:absolute;height:100%;background-color:rgba(255,255,255,0.4);top:0;left:0}.DataTable-loading.no-items{min-height:200px}.DataTable-loading.no-items td{border-bottom:none}

.hb-back-to-top{position:fixed;bottom:15px;right:20px;z-index:20;opacity:0;transition:opacity 0.3s ease}.hb-back-to-top.active{transition:opacity 0.3s ease;cursor:pointer;opacity:0.37}.hb-back-to-top.active:hover{opacity:0.72}

.MenuPopover-arrow{top:-7px;z-index:1;width:12px;right:20px;height:12px;content:'';position:absolute;border-radius:0 0 4px 0;-webkit-transform:rotate(-135deg);transform:rotate(-135deg);background:white;border-right:solid 1px #919eab25;border-bottom:solid 1px #919eab25}

.hb-max-line{display:-webkit-box;-webkit-box-orient:vertical;overflow:hidden;word-break:break-word;white-space:pre-line}

.toast-container{box-sizing:border-box;position:fixed;top:20px;right:-320px;width:660px;z-index:9999}.toast{width:320px;margin-left:340px;transition:all 0.4s ease}.toast.slide-in{margin-left:0}

