:500;line-height:16px;letter-spacing:-0.01em}@media screen and (max-width: 860px){h6{font-size:14px;line-height:17px}}@media screen and (max-width: 480px){h6{font-size:16px;line-height:19px}}@font-face{font-family:'Material Icons';font-style:normal;font-weight:400;src:local("Material Icons"),local("MaterialIcons-Regular"),url(/static/media/MaterialIcons-Regular.69a05f7f.ttf) format("truetype")}.material-icons{font-family:'Material Icons';font-weight:normal;font-style:normal;font-size:24px;display:inline-block;line-height:1;text-transform:none;letter-spacing:normal;word-wrap:normal;white-space:nowrap;direction:ltr;-webkit-font-smoothing:antialiased;text-rendering:optimizeLegibility;-moz-osx-font-smoothing:grayscale;-webkit-font-feature-settings:'liga';font-feature-settings:'liga'}

